* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
}
@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.container {
  margin: 0 auto;
  max-width: 1100px;
}
.body {
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  background-color: #ffffff;
  min-height: 100vh;
  display: grid;
  grid-column: 1fr;
  grid-template-rows: min-content 1fr min-content;
}
.body--scrolloff {
  overflow: hidden;
}
.main {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(2, min-content);
  grid-row-gap: 40px;
}
.header {
  height: 100px;
}
.footer {
  height: 100px;
}
.top-five {
  height: min-content;
  width: 100%;
}
.top-five__title {
  padding: 20px;
  text-align: left;
  font-weight: bold;
  font-size: 28px;
  text-transform: capitalize;
}
.top-five__list {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(5, 1fr);
  grid-row-gap: 20px;
}
.top-five__item {
  width: 100%;
  position: relative;
  display: grid;
  grid-template-columns: 180px 140px repeat(3, 1fr);
  grid-template-areas: 'i t b s m';
  color: #ffffff;
  background: #002d4a;
  background: linear-gradient(0deg, #002d4a 0%, rgba(0, 36, 61, 0.87438725) 100%);
}
.top-five__name-wrap {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 5px 10px;
  grid-area: t;
}
.top-five__name {
  font-size: 20px;
  font-weight: normal;
  text-align: center;
  text-transform: capitalize;
  text-align: left;
  color: #ffffff;
}
.top-five__currency-info {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  grid-area: i;
}
.top-five__currency-block {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;
  grid-row-gap: 4px;
  justify-items: center;
  padding: 10px;
}
.top-five__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.top-five__currency-name {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-weight: bold;
  font-size: 42px;
  text-transform: uppercase;
  color: #ffffff;
}
.top-five__price-title {
  font-size: 20px;
  font-weight: normal;
  text-transform: capitalize;
  color: #DCDCDC;
  align-self: end;
}
.top-five__price {
  width: 100%;
  text-align: center;
  font-size: 38px;
  font-weight: bold;
  color: #ffffff;
}
.top-five__price--error {
  font-size: 22px;
  color: tomato;
  opacity: 0.8;
}
.top-five__buy {
  border-right: 2px dotted #808080;
  border-left: 2px dotted #808080;
  grid-area: b;
}
.top-five__sell {
  border-right: 2px dotted #808080;
  grid-area: s;
}
.top-five__average {
  grid-area: m;
}
.converter {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url('../../img/converter-bg.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}
.converter__wrap {
  padding: 100px 20px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(2, min-content);
  grid-row-gap: 20px;
}
.converter__title {
  font-size: 28px;
  font-weight: bold;
  text-align: left;
  text-transform: capitalize;
  color: #ffffff;
}
.converter__module {
  justify-self: center;
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 15px;
  padding: 50px;
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.8);
}
.converter__radio {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;
}
.converter__radio-label {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 5px;
  color: #000000;
  cursor: pointer;
}
.converter__radio-text {
  font-size: 18px;
  text-transform: capitalize;
  color: #000000;
  opacity: 0.7;
}
.converter__buy-sell-radio {
  position: absolute;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
.converter__custom-radio {
  position: relative;
  cursor: pointer;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background: transparent;
  border: 3px solid #808080;
  opacity: 0.8;
}
.converter__radio-label:hover .converter__custom-radio,
.converter__radio-label:focus .converter__custom-radio {
  opacity: 1;
}
.converter__buy-sell-radio:checked + .converter__custom-radio::before {
  content: '';
  position: absolute;
  z-index: 1;
  width: 10px;
  height: 10px;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  background-color: #808080;
  opacity: 1;
}
.converter__buy-sell-radio:checked + .converter__custom-radio {
  opacity: 1;
}
.converter__buy-sell-radio:checked ~ .converter__radio-text {
  opacity: 1;
}
.converter__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}
.converter__select {
  width: 180px;
  border: none;
  height: 30px;
  padding: 0 8px;
  border-radius: 5px;
  cursor: pointer;
}
.converter__input {
  border: none;
  background-color: #ffffff;
  height: 30px;
  padding: 0 8px;
  border-radius: 5px;
  width: 180px;
  cursor: pointer;
  transition: all 0.2s ease;
}
.converter__select:hover,
.converter__input:hover,
.converter__select:focus,
.converter__input:focus {
  box-shadow: 5px 5px 14px #bebebe, -5px -5px 14px #ffffff;
  transition: all 0.2s ease;
}
